import { renderPlans, firstPlan, secondPlan } from './data';

export default function Planes() {
    return (
        <section className='py-10 bg-white font-sans relative'>

            <img src={require('./images/bg.png')} className='absolute w-full h-full object-cover right-0 top-0' />

            <div className='container !py-20'>

                <h1 className='font-bold text-[30px] text-[#3F2621] mb-10 text-center'>Planes</h1>

                <div className='grid grid-cols-12 gap-5'>

                    <div className='col-span-12 lg:col-span-6 flex items-center justify-center flex-col'>

                        <div className='bg-[#C9EBFA] rounded-[200px] min-h-[560px] py-10 px-3 lg:p-10'>

                            <img src={require('./images/logo-text.PNG')} className='max-w-full m-auto mb-10' />

                            {renderPlans(firstPlan)}

                            <div className='mt-10 text-center capitalize'>

                                <h1 className='font-bold'>10% sobre</h1>
                                <h1 className='font-bold text-[30px] mt-[-5px]'>compras online</h1>

                            </div>

                        </div>

                        <div className='my-16 hidden lg:block'>

                            <h5 className='font-medium text-[#58291E] text-center lg:max-w-[50%] m-auto pb-5 border-b border-[#58291E]'>Tanto el Marketplace como las suscripciones cuentan con la automatización de pagos y logística</h5>

                            <h5 className='font-medium text-[#58291E] text-center lg:max-w-[50%] pt-5 m-auto'>Las compras entre tostador y clientes son directas, no vamos a secuestrar vuestro dinero.  </h5>

                        </div>

                    </div>

                    <div className='col-span-12 lg:col-span-6 flex items-center justify-center flex-col'>

                        <div className='bg-[#FFF5EF] rounded-[200px] py-10 px-3 lg:p-10 min-h-[560px] flex items-center justify-center flex-col'>

                            <img src={require('./images/logo.png')} className='max-w-full m-auto mb-10' />

                            {renderPlans(secondPlan)}

                            <div className='mt-10 text-center capitalize'>

                                <img src={require('./images/price.png')} alt='' className='m-auto' />

                                <h1 className='font-bold'>establecimiento</h1>

                            </div>

                        </div>

                        <div className='my-16 hidden lg:block'>

                            <h5 className='font-medium text-[#58291E] text-center lg:max-w-[50%] m-auto pb-5 border-b border-[#58291E]'>Las compras realizadas a través de la aplicación tendrán una comisión por servicio.</h5>

                            <h5 className='font-medium text-[#58291E] text-center lg:max-w-[50%] pt-5 m-auto'>Los costes de los envíos irán imputados al cliente, no tienes que preocuparte por ellos. </h5>

                        </div>

                    </div>

                    <div className='block lg:hidden col-span-12'>

                        <div className='sm:w-[70%] w-full m-auto'>

                            <div className='my-5 lg:my-16'>

                                <h5 className='font-medium text-[#58291E] text-center m-auto mb-2'>Tanto el Marketplace como las suscripciones cuentan con la automatización de pagos y logística</h5>

                                <h5 className='font-medium text-[#58291E] text-center m-auto'>Las compras entre tostador y clientes son directas, no vamos a secuestrar vuestro dinero.  </h5>

                            </div>

                            <div className='my-5 lg:my-16 pt-8 border-t border-[#58291E]'>

                                <h5 className='font-medium text-[#58291E] text-center m-auto mb-2'>Las compras realizadas a través de la aplicación tendrán una comisión por servicio.</h5>

                                <h5 className='font-medium text-[#58291E] text-center m-auto'>Los costes de los envíos irán imputados al cliente, no tienes que preocuparte por ellos. </h5>

                            </div>

                        </div>

                    </div>

                </div>

            </div>

        </section>
    )
}
