import { faCheck, faClose } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'


const firstPlan = [
    { name: 'Marketplace', status: true },
    { name: 'Pasaporte', status: true },
    { name: 'Gestión de ofertas personalizadas', status: false },
    { name: 'Gestión de suscripción', status: false },
    { name: 'Dashboard Analytics', status: false },
];
const secondPlan = [
    { name: 'Marketplace', status: true },
    { name: 'Pasaporte', status: true },
    { name: 'Gestión de ofertas personalizadas', status: true },
    { name: 'Gestión de suscripción', status: true },
    { name: 'Dashboard Analytics', status: true },
];

const renderPlans = plan => {
    return plan.map((item, index) => {

        return (
            <div className={`flex items-center border-t border-[#58291E] p-2 py-3 lg:w-[300px] ${index === (plan.length - 1) ? "border-b" : ""}`}>

                <div className={`w-[22px] ${item.status ? "bg-[#45B8EA]" : "bg-[#3F2621]"} text-white rounded-[6px] me-5 flex items-center justify-center font-medium p-[1px]`}>

                    <FontAwesomeIcon icon={item.status ? faCheck : faClose} className='text-[11px]' />

                </div>

                <p>{item.name}</p>

            </div>
        )

    })
};

export {
    firstPlan,
    secondPlan,
    renderPlans
}